@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
html{
  overflow-x: hidden;

}
body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  background-color: black;
  background: rgb(123, 143, 148);
  background: repeating-linear-gradient(
    45,
    rgba(123, 143, 148, 1) 0%,
    rgba(108, 114, 115, 1) 48%,
    rgba(123, 143, 148, 1) 100%
  );
  overflow-x: hidden;
}

.vh-50 {
  height: 50vh;
}

.ph-1r {
  padding: 0rem 1rem;
}

.active-nav-link {
  color: #fff !important;
}

.preview{display:flex;cursor:pointer;border:1px solid rgba(0,0,0,0.125);border-radius:0.25rem;border-color:#fff;max-height:200px;padding:1rem 1rem;margin:auto;width:100%;padding:0.2rem}.content{margin-left:0.5rem;display:flex;align-items:center}.link{margin-top:.65rem;width:100%;color:white}.link:hover{color:white;text-decoration:none;border-color:#00bc8c}.title{font-size:2rem;vertical-align:center;margin:0;margin-left:0.5rem}.icon{font-size:2rem !important}

.home-page{display:flex;flex-wrap:wrap;padding:0rem 5px;padding-top:0.5rem}

.post{background-color:#303030;background-clip:border-box;border:1px solid rgba(0,0,0,0.125);border-radius:.25rem;border-color:#00bc8c !important}.blog-posts-list{display:flex;flex-direction:column;align-items:flex-end}.post-img{border-radius:.25rem;border:1px solid rgba(0,0,0,0.125)}.img-container{border:1px solid rgba(0,0,0,0.125);border-radius:.25rem}@media screen and (min-width: 700px){.post{margin-bottom:1rem;padding:1rem;width:60%;display:flex;height:300px}.post .img-container{height:200px;align-self:center}.post .img-container .post-img{height:100%}.post .post-content-preview{height:250px;padding:10px;align-self:center}.post .post-content-preview p{line-height:30px}}


.header{z-index:1000;position:fixed;left:0;right:0;height:50px;display:flex;align-items:center;padding:0rem 0.25rem;justify-content:space-between;background-color:#222;box-shadow:0px -10px 16px 0px}.brand-container{height:100%;display:flex;align-items:center;text-decoration:none}.brand-container :hover{text-decoration:none}.brand-logo{height:90%}.brand-name{padding:0rem 0.5rem}.spacer{height:50px;width:100vw}a:hover{text-decoration:none !important;color:#00bc8c}

body{background-color:#222}.navbar{padding:0.25rem 1rem}.name{color:#00bc8c;opacity:0.75}a{text-decoration:none;color:#00bc8c}

