@import url('https://fonts.googleapis.com/css2?family=Raleway&display=swap');
html{
  overflow-x: hidden;

}
body {
  font-family: 'Raleway', sans-serif;
  margin: 0;
  background-color: black;
  background: rgb(123, 143, 148);
  background: repeating-linear-gradient(
    45,
    rgba(123, 143, 148, 1) 0%,
    rgba(108, 114, 115, 1) 48%,
    rgba(123, 143, 148, 1) 100%
  );
  overflow-x: hidden;
}

.vh-50 {
  height: 50vh;
}

.ph-1r {
  padding: 0rem 1rem;
}

.active-nav-link {
  color: #fff !important;
}
